import Iterator from 'Iterator'

class RequestQueue extends Iterator {
  constructor(requestOptions, net) {
    super()
    this.resolveStack = []
    this.rejectStack  = []
    this.promises = requestOptions.map((option) => new Promise((resolve, reject) => {
      this.resolveStack.push(resolve)
      this.rejectStack.push(reject)
    }))

    this.net = net
    this.items = requestOptions
    this.start()
  };

  start() {
    this.rewind()
    this.nextRequest()
  }

  nextRequest() {
    this.next()
    if(this.valid()) {
      let key = this.key,
        resolve = this.resolveStack[key],
        reject  = this.rejectStack[key],
        currentOption = this.current()

      if (currentOption.length > 1) {
        throw Error('[RedPanda] Queue doesn\'t stack with other queue')
      }

      this.net.send(currentOption)
        .first()
        .then((data) => {
          this.nextRequest()
          resolve(data)
        })
        .catch((err) => reject(err))
    }
  };
}

export default RequestQueue



