class Iterator {
  constructor(items) {
    this.items = items
    this.rewind()
  };

  next() {
    this.key++
  }

  rewind() {
    this.key = -1
  }

  current() {
    return this.items[this.key]
  }

  count() {
    return this.items.length
  }

  valid() {
    return this.key >= 0 && this.key < this.count()
  }
}

export default Iterator



